import React, { useState, useEffect } from "react";
import Countdown from 'react-countdown';

 import './Components.css';

import Display from './Display';


const Clock = ({time,map,rounds}) => {
  const [toggle, setToggle] = useState(false);

    var i=-1
    var j=1
    var sets=0

const Completionist = () =>   {
  if(i==1&&j<rounds){
    console.log(j,rounds)
    // setJ(j+1)
    j++
    i=-1
  }
  else if(i==2){
    // setJ(1)
    sets++
    j=1
    i=-1
  }
  i++
   return <Countdown date={Date.now() + map[i]*1000} zeroPadTime={1} renderer={renderer}   ref={myRef}/>

 }

var myRef = React.createRef();

const renderer = ({hours, minutes, seconds, completed }) => {
  if (completed)
        return <Completionist />;
  else {
    // Render a countdown
    if(minutes>0)
    return <Display sets={sets} total={map[i]} toggle={toggle} minutes={minutes} seconds={seconds} j={j} rounds={rounds} myRef={myRef} i={i}/>;

    else
      return <Display sets={sets} total={map[i]} toggle={toggle} seconds={seconds} j={j} rounds={rounds} myRef={myRef} i={i}/>;
  }
};

  return (
    <div className="inner-app-wrapper">

    <Countdown
    date={Date.now() + 5000}
    renderer={renderer}/>
  </div>

  );
}

export default Clock;
